<template>
  <div class="modal" @click="closeModal">
    <div class="modal__block" @click.stop="">
      <h2 class="modal__block-title">
        {{ !edit ? $t('addNote') : $t('changeNote') }}
      </h2>
      <div class="modal__block-inputs">
        <label>
          <span>Title</span>
          <input type="text" placeholder="Title" v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <input type="text" placeholder="Content" v-model="text" />
        </label>
      </div>
      <div class="modal__block-btns">
        <button class="modal__block-btns-btn del" @click="closeModal">
          {{ $t('cansel') }}
        </button>
        <button class="modal__block-btns-btn edit" @click="addNote" v-if="!edit">
          {{ $t('addNote') }}
        </button>
        <button class="modal__block-btns-btn edit" @click="changeNote" v-else>{{ $t('changeNote') }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
export default {
  props: {
    editedNote: Object,
    edit: Boolean,
  },
  data() {
    return {
      title: '',
      text: '',
    }
  },
  methods: {
    closeModal() {
      this.$emit('close')
      this.title = this.text = ''
    },
    addNote() {
      if (this.title != '' && this.text != '') {
        const note = {
          id: uuidv4(),
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        }
        this.$emit('addNote', note)
        this.closeModal()
      }
    },
    changeNote() {
      if (this.title != '' && this.text != '') {
        const newEditedNote = {
          id: this.editedNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        }
        this.$emit('changeNote', newEditedNote)
        this.closeModal()
      }

    }
  },
}
</script>

<style lang="scss" scoped></style>
