<template>
  <div class="wrapper">
    <Navbar @setSearch="searchValue = $event" />
    <Notes :notes="filterNotes" @change="change" @delNote="delNote" />
    <AddNote @click="modalOpen = true, edit = false" />
    <Modal v-show="modalOpen" @close="modalOpen = false" @addNote="addNote" :edit="edit" :editedNote="editedNote"
      @changeNote="changeNote" />
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import Notes from '@/components/Notes.vue'
import AddNote from '@/components/AddNote.vue'
import Modal from '@/components/Modal.vue'
export default {
  components: {
    Navbar,
    Notes,
    AddNote,
    Modal,
  },
  data() {
    return {
      modalOpen: false,
      notes: [],
      edit: false,
      editedNote: null,
      searchValue: ''
    }
  },
  methods: {
    addNote(note) {
      this.notes = [...this.notes, note]
    },
    change(id) {
      this.modalOpen = this.edit = true
      let currentNote = this.notes.find((note) => note.id == id)
      this.editedNote = currentNote
    },
    changeNote(newNote) {
      this.notes.forEach((note) => {
        if (note.id == newNote.id) {
          note.title = newNote.title
          note.text = newNote.text
          note.date = newNote.date
        }
      })
    },
    delNote(id) {
      let index = this.notes.findIndex((note) => note.id == id)
      this.notes.splice(index, 1)
    },
    getNotes() {
      let localNotes = localStorage.notes
      if (localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    }
  },
  mounted() {
    this.getNotes()
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true
    }
  },
  computed: {
    filterNotes() {
      return this.searchValue
        ? this.notes.filter((note) => note.title.toLowerCase().includes(this.searchValue.toLowerCase()))
        : this.notes
    }
  }
}
</script>
